.navbar{
	padding: 5px;
	width: 99.2%;
	height: 20px;
  font-size: .9rem;
	color: #ffffff;
  background-color: #000000;
  object-fit: cover;
}

a[type="nav"] {
    color: #ffffff;
    display:inline-block;
    text-decoration: none;
	font-weight: 400;
}
a[type="deactivate"] {
    color: #A4A4A4;
    display:inline-block;
    text-decoration: none;
	font-weight: 400;
}
a[type="text"] {
    color: #ffffff;
    display:inline-block;
    text-decoration: none;
	font-weight: 400;
}

.underlineHoverstatic:after {
    display: block;
    left: 0;
    bottom: -10px;
    width: 0;
    height: 2px;
    background-color: #56baed;
    content: "";
    transition: width 0.2s;
  }
.underlineHoverstatic:hover:after{
width: 100%;
}
	
.underlineHovernav:after {
    display: block;
    left: 0;
    bottom: -10px;
    width: 0;
    height: 2px;
    background-color: #56baed;
    content: "";
    transition: width 0.2s;
  }
  
.underlineHovernav:hover {
color: #56baed;
}

.underlineHovernav:hover:after{
width: 100%;
}

.navbar-text {
	margin-top: 15px;
	margin-bottom: 15px
}

.navbar-text {
	float: left;
	margin-right: 15px;
	margin-left: 15px;
}

.navbar-left {
	float: left!important;
	padding: left;
	padding-right: 15px;
}

.navbar-right {
	float: right!important;
	margin-right: 0px;
	padding-left: 15px;
}
.navbar-middle {
  float: center!important;
	margin-left: 10px;
}


.navbar-nav>li>.dropdown-menu {
	margin-top: 0;
	border-top-left-radius: 0;
	border-top-right-radius: 0
}

.navbar-nav {
	float: left;
	margin: 0
}

.navbar-nav>li {
	float: left
}