body {
  font-family: "Poppins", sans-serif;
  background-color: #fff;
  font-size: .9rem;
}
html {
  background-color: #fff;
}
  #cpagecontent {
    margin-left: 0px;
    background: #fff;
    width: 100%;
    /* -webkit-box-shadow: 0 10px 30px 0 rgba(95,186,233,0.4);
    box-shadow: 0 10px 30px 0 rgba(95,186,233,0.4); */
    
    vertical-align: top;
    height: 100%;
    max-width: 1350px;
    object-fit: cover;
    overflow: auto;
    position: relative;
    text-align: center;
    -webkit-box-shadow: 0 30px 60px 0 rgba(0, 0, 0, 0.383);
    box-shadow: 0 30px 60px 0 rgba(0, 0, 0, 0.329);
  }
  h2[class="mp"]{
    color: #0d0d0d;
  }
  div [type="blue"]{
    vertical-align: top;
    padding : 1px;
    margin-left: 7px;
    width : 98.7%;
    -webkit-border-radius: 10px 10px 10px 10px;
    border-radius: 10px 10px 10px 10px;
  }
  div [type="bluefull"]{
    padding : 1px;
    -webkit-border-radius: 10px 10px 10px 10px;
    border-radius: 10px 10px 10px 10px;
  }
  .makefull{
    width:100%;
  }
  .wrappercp {
    display: flex;
    align-items: center;
    flex-direction: column; 
    justify-content: center;
    width: 100%;
    min-height: 100%;
  }
  
  textarea {
    overflow: auto;
    resize: none;
    font-size: 1rem;
    letter-spacing: 1px;
}
textarea {
    padding: 10px;
    line-height: 1.5;
    border-radius: 5px;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px #999;
}
div[type = "ppage"] {
  max-width: 850px;
  /* max-height: 100px; */
  max-height: fit-content;
}



table {
    border-collapse: collapse;
    width: 100%;
    vertical-align: top;
  }

  th, td {
    text-align: left;
    padding: 8px;
    vertical-align: top;
  }
  th[type="center"], td[type="center"]{
    text-align: center;
  }
  td [class="fortable"]{
    padding-top: 8px;
    padding-bottom:  8px;
    padding-left:  0px;
    padding-right:  0px;
  }

  tr:nth-child(even){background-color: #f2f2f2}

  th {
    vertical-align: top;
    background-color: #5fbae9;
    color: white;
  }
  select{
    background-color: #5fbae9;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    text-transform: uppercase;
    font-size: 13px;
    -webkit-border-radius: 5px 5px 5px 5px;
    border-radius: 5px 5px 5px 5px;
  }
    
  div[type=buttongreen] {
    background-color: #2ED320;
    border: none;
    color: white;
    padding: 10px 50px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    text-transform: uppercase;
    font-size: 13px;
    -webkit-border-radius: 5px 5px 5px 5px;
    border-radius: 5px 5px 5px 5px;
  }
  div[type=buttongreensidepane] {
    background-color: #2ED320;
    border: none;
    color: white;
    width: 160px;
    padding: 10px 50px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    text-transform: uppercase;
    font-size: 13px;
    border-radius: 5px 5px 5px 5px;
  }
  
  